.reaction-items {
  list-style: none;
  margin: 0;
  padding: 10px;
  position: absolute;
  background-color: rgba(29, 29, 31, 0.8);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  top: -60px;
  left: 50%;
  transition: all 150ms ease-in-out;
  transform: scale(0) translateX(-50%);
  transform-origin: bottom;
  opacity: 0;
  visibility: hidden;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  z-index: 100;
}

.reaction-items.open {
  transform: scale(1) translateX(-50%);
  opacity: 1;
  visibility: visible;
}

.reaction-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  font-size: 20px;
  color: #fff;
  background-color: #6366f1;
  transition: all 0.2s ease-in-out;
}

.reaction-item:hover {
  transform: scale(1.1);
  background-color: #4338ca;
}

.myIcon a.pinIcon {
  opacity: 0;
  color: grey;
  transition: opacity 0.5s ease;
}

.myIcon a.pinIcon:hover,
.myIcon a.pinIcon.active {
  opacity: 1;
  color: blue;
}
